import { api } from '../api/api';

let initialState = {
  IP: '',
  Weather: {},
  isChoosingLocation: false,
  Places: {},
};
interface IAction {
  type: string;
  IP: string;
  Weather: any;
  Places: { name: string; country: string }[];
  isChoosingLocation: boolean;
}

const SET_IP = 'main/SET_IP';
const SET_IS_CHOSING_LOCATION = 'main/SET_IS_CHOSING_LOCATION';
const SET_PLACES = 'main/SET_PLACES';
const GET_WEATHER = 'main/GET_WEATHER';

const MainReducer = (state = initialState, action: IAction) => {
  switch (action.type) {
    case SET_IP:
      return {
        ...state,
        IP: action.IP,
      };
    case SET_PLACES:
      return {
        ...state,
        Places: action.Places,
      };
    case GET_WEATHER:
      return {
        ...state,
        Weather: action.Weather,
      };
    case SET_IS_CHOSING_LOCATION:
      return {
        ...state,
        isChoosingLocation: action.isChoosingLocation,
      };
    case GET_WEATHER:
      return {
        ...state,
        Weather: action.Weather,
      };

    default:
      return state;
  }
};

const SetIp = (IP: string) => ({
  type: SET_IP,
  IP,
});
const SetWeather = (Weather: IAction) => ({
  type: GET_WEATHER,
  Weather,
});
const SetIsChoosingLocation = (isChoosingLocation: IAction) => ({
  type: SET_IS_CHOSING_LOCATION,
  isChoosingLocation,
});
const SetPlacesAPI = (Places: IAction) => ({
  type: SET_PLACES,
  Places,
});

export const GetWeatherByIP = () => async (dispatch: Function) => {
  const IP: string = await api.GetIP();
  dispatch(SetIp(IP));
  dispatch(GetWeather(IP));
};
export const GetWeather = (IP: string) => async (dispatch: Function) => {
  let Weather: any = await api.GetWeather(IP);
  let Place = Weather.location;
  Weather = Weather.current;

  let DateNow: any = new Date(Date.now());
  const DateNum = DateNow.getDate();
  const Day = DateNow.getDay();
  var DayNames = [
    'Sunday',

    'Monday',
    'Tuesday',
    'Wednesday',
    'Thursday',
    'Friday',
    'Saturday',
  ];
  const Month = DateNow.getMonth();
  const monthNames = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];
  DateNow = `${DayNames[Day]}, ${DateNum} ${monthNames[Month]}`;
  Weather.DateNow = DateNow;
  Weather.Place = Place;
  dispatch(SetWeather(Weather));
};
export const GetPlaces = (placeN: string) => async (dispatch: Function) => {
  const Places: IAction = await api.GetPlace(placeN);
  dispatch(SetPlacesAPI(Places));
};

export default MainReducer;
